.ResetWrapper {
    height: 100vh;
    display: grid;
    place-items: center;
}

.ResetWrapper form {
    height: 33vh;
    width: 30vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.51rem;
}

.ResetWrapper form input {
    height: 2.7rem;
    width: 100%;
    border-radius: 0.41rem;
    padding-left: 1rem;
    margin-bottom: 1.2rem;
    border: 1px solid hsl(0, 0%, 0%, 0.5);
}

.ResetWrapper form input[type="submit"] {
    width: 80%;
    padding-left: 0rem;
    background-color: var(--golden-color);
    font-size: 1rem;
    font-weight: normal;
    border: none;
    color: rgb(61, 61, 61);
    cursor: pointer;
}

.ResetWrapper form p {
    align-self: flex-start;
}

@media screen and (max-width: 720px) {
    .ResetWrapper form {
        height: 33vh;
        width: 80vw;
    }
}